import Vue from 'vue';
import { sendJsonRequest } from '../../services/utility-functions/send-json-request';
const roleInfoURL =
    'https://raw.githubusercontent.com/TheFinalSkill/AmongUsChallenges/main/Mods/Roledescriptions.json';
export default Vue.extend({
    data() {
        return {
            link: 'https://github.com/polusgg/Town-Of-Us',
            roleInfo: {} as Roleinfo,
        };
    },
    methods: {
        getData: async function () {
            const roleInfo = await sendJsonRequest<Roleinfo>(roleInfoURL);
            this.roleInfo = roleInfo;
        },
    },
    async mounted() {
        await this.getData();
    },
});

interface Roleinfo {
    Crewmate: Party;
    Neutral: Party;
    Impostor: Party;
    Modifiers: Party;
}

interface Party {
    name: string;
    variant: string;
    roles: Role[];
}

interface Role {
    name: string;
    description: string;
}
